
body {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  background-color: #FED;
  min-height: 100vh;
  color: #00152e;
  font-family: 'Cascadia';
}

code {
  width: 23.8em;
  height: 23.8em;
  font-family: 'Spezia Mono';
  font-size: 1rem;
  font-variation-settings: 'wght' 0;
  overflow: hidden;
}
